import Vue from 'vue'
import Router from 'vue-router'
import helloPage from '@/components/helloPage'
import networkResources from '@/components/networkResources'
import trafficAnalysis from '@/components/trafficAnalysis'
import bdmap from "@/components/bdMap"

import vendorCount from "@/components/networkResources/OLT/vendorCount"
import modelCount from "@/components/networkResources/OLT/modelCount"
import oltUsersCount from "@/components/networkResources/OLT/oltUsersCount"
import usersStatA from "@/components/networkResources/OLT/usersStatA"
import usersStatIPTV from "@/components/networkResources/OLT/usersStatIPTV"
import usersStat from "@/components/networkResources/OLT/usersStat"

import ponCount from "@/components/networkResources/PON/ponCount"
import serviceBoardStat from "@/components/networkResources/PON/serviceBoardStat"
import ponType from "@/components/networkResources/PON/ponType"
import vendorPonType from "@/components/networkResources/PON/vendorPonType"
import trafficStat from "@/components/networkResources/PON/trafficStat"
import ponUsersStat from "@/components/networkResources/PON/ponUsersStat"

import portCount from "@/components/networkResources/Upper/portCount"
import trafficRate from "@/components/networkResources/Upper/trafficRate"

import ONUType from "@/components/networkResources/ONU/ONUType"
import ONUnotMatch from "@/components/networkResources/ONU/ONUnotMatch"

import baseInfo from "@/components/networkResources/baseInfo"

import upstreamTraffic from "@/components/trafficAnalysis/traffic/upstreamTraffic"
import downstreamTraffic from "@/components/trafficAnalysis/traffic/downstreamTraffic"
import averageTrafficRate from "@/components/trafficAnalysis/traffic/averageTrafficRate"
import upstreamTrafficPerPackage from "@/components/trafficAnalysis/traffic/upstreamTrafficPerPackage"
import downstreamTrafficPerPackage from "@/components/trafficAnalysis/traffic/downstreamTrafficPerPackage"

import upstreamTrafficNotMatch from "@/components/trafficAnalysis/ONU/upstreamTrafficNotMatch"
import downstreamTrafficNotMatch from "@/components/trafficAnalysis/ONU/downstreamTrafficNotMatch"
import upstreamTrafficMatch from "@/components/trafficAnalysis/ONU/upstreamTrafficMatch"
import downstreamTrafficMatch from "@/components/trafficAnalysis/ONU/downstreamTrafficMatch"
import upstreamTrafficTrends from "@/components/trafficAnalysis/ONU/upstreamTrafficTrends"
import downstreamTrafficTrends from "@/components/trafficAnalysis/ONU/downstreamTrafficTrends"

// 新加
import FlowDistribution from '@/components/networkResources/USER_NEED/FlowDistribution'
import PonFlowSpeed from '@/components/networkResources/USER_NEED/PonFlowSpeed'
import DiffCombPonSpeed from '@/components/networkResources/USER_NEED/DiffCombPonSpeed'
import PonFlowTrend from '@/components/networkResources/USER_NEED/PonFlowTrend'

// login
import Login from '@/components/login'
import store from '@/store/store'

// task
import Task from '@/components/task/task'
import Ts21 from '@/components/task/two/ts21'
import Ts22 from '@/components/task/two/ts22'
import Ts23 from '@/components/task/two/ts23'
import Ts24 from '@/components/task/two/ts24'
//import mainc from '@/components/mainC'
//import businessModel from '@/components/business-model'
Vue.use(Router)

const routes = [
  {
    path: '/',
    name: '主页',
    meta: {
      requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
    },
    components: {
      homePage:helloPage
    }
  },
  {
    path: '/login',
    name: '登录',
    components:{
      login:Login
    }
  },
  {
    path: '/businessModel',
    name: '业务模型',
    meta: {
      requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
    },
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/userHabits',
    name: '用户行为习惯',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/chartDownload',
    name: '总表下载',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/networkArchitecture',
    name: '网络架构',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/map',
    name: '地图展示',
    components: {
      businessModel:bdmap
    }
  },
  {
    path: '/networkResources',
    name: '网络资源统计',
    components: {
      // mainleft:sidemenu,
      businessModel:networkResources,
    },
    children:[
      { path: '/vendorCount',
        name:'OLT数量及各厂家占比',
        components: {
          appRight:vendorCount
        },
      },
      { path: '/modelCount',
        name:'各型号占比',
        components: {
          appRight:modelCount
        },
      },
      { path: '/oltUsersCount',
        name:'OLT下挂用户分布',
        components: {
          appRight:oltUsersCount
        },
      },
      { path: '/usersStatA',
      name:'分业务带宽的用户数分布',
      components: {
        appRight:usersStatA
      },
    },
      { path: '/usersStatIPTV',
        name:'IPTV用户数分布',
        components: {
          appRight:usersStatIPTV
        },
      },
      { path: '/usersStat',
        name:'分业务类型的用户数分布',
        components: {
          appRight:usersStat
        },
      },
      { path: '/ponCount',
        name:'PON口数量及利用率',
        components: {
          appRight:ponCount
        },
      },
      { path: '/serviceBoardStat',
        name:'业务板槽位利用率分布',
        components: {
          appRight:serviceBoardStat
        },
      },
      { path: '/ponType',
        name:'PON口制式比例',
        components: {
          appRight:ponType
        },
      },
      { path: '/vendorPonType',
        name:'PON口制式厂家比例',
        components: {
          appRight:vendorPonType
        },
      },
      { path: '/trafficStat',
        name:'PON口流量利用率',
        components: {
          appRight:trafficStat
        },
      },
      { path: '/ponUsersStat',
        name:'PON口用户携带占比',
        components: {
          appRight:ponUsersStat
        },
      },
      { path: '/portCount',
        name:'100M与GE与10GE端口数',
        components: {
          appRight:portCount
        },
      },
      { path: '/trafficRate',
        name:'上联口流量分布',
        components: {
          appRight:trafficRate
        },
      },
      { path: '/baseInfo',
        name:'设备信息',
        components: {
          appRight:baseInfo
        },
      },

      { path: '/ONUType',
        name:'三种ONU的比例',
        components: {
          appRight:ONUType
        },
      },
      { path: '/ONUnotMatch',
      name:'ONU不匹配的用户数',
      components: {
        appRight:ONUnotMatch
      },
      },
      {
        path: 'userNeed/flowDistrobution',
        components: {
          appRight: FlowDistribution
        }
      },
      {
        path: 'userNeed/PonFlowSpeed',
        components: {
          appRight: PonFlowSpeed
        }
      },
      {
        path: 'userNeed/DiffCombPonSpeed',
        components: {
          appRight: DiffCombPonSpeed
        }
      },
      {
        path: 'userNeed/PonFlowTrend',
        components: {
          appRight: PonFlowTrend
        }
      },
    ]
  },
  {
    path: '/trafficCollect',
    name: '业务流量采集',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/trafficAnalysis',
    name: '业务流量分析',
    components: {
      // mainleft:sidemenu,
      businessModel:trafficAnalysis,
    },
    children:[
      { path: '/upstreamTraffic',
        name:'人均上行流量',
        components: {
          appRight:upstreamTraffic
        },
      },
      { path: '/downstreamTraffic',
        name:'人均下行流量',
        components: {
          appRight:downstreamTraffic
        },
      },
      { path: '/averageTrafficRate',
        name:'上下行流量的比例',
        components: {
          appRight:averageTrafficRate
        },
      },
      { path: '/upstreamTrafficPerPackage',
        name:'不同套餐的平均上行流量',
        components: {
          appRight:upstreamTrafficPerPackage
        },
      },
      { path: '/downstreamTrafficPerPackage',
        name:'不同套餐的平均下行流量',
        components: {
          appRight:downstreamTrafficPerPackage
        },
      },
      { path: '/upstreamTrafficNotMatch',
        name:'不匹配用户的平均上行流量',
        components: {
          appRight:upstreamTrafficNotMatch
        },
      },
      { path: '/downstreamTrafficNotMatch',
        name:'不匹配用户的平均下行流量',
        components: {
          appRight:downstreamTrafficNotMatch
        },
      },
      { path: '/upstreamTrafficMatch',
        name:'匹配用户的平均上行流量',
        components: {
          appRight:upstreamTrafficMatch
        },
      },
      { path: '/downstreamTrafficMatch',
        name:'匹配用户的平均下行流量',
        components: {
          appRight:downstreamTrafficMatch
        },
      },
      { path: '/upstreamTrafficTrends',
        name:'人均上行流量变化趋势',
        components: {
          appRight:upstreamTrafficTrends
        },
      },
      { path: '/downstreamTrafficTrends',
        name:'人均下行流量变化趋势',
        components: {
          appRight:downstreamTrafficTrends
        },
      },
    ]
  },
  {
    path: '/trafficEstimate',
    name: '业务流量估算',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/capacityAnalysis',
    name: '现网承载能力分析',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/serviceCapacityAnalysis',
    name: '业务对网络承载能力分析',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/planEvaluation',
    name: '规划建设方案评估',
    components: {
      businessModel:helloPage
    }
  },
  {
    path: '/task',
    name: '任务',
    components: {
      businessModel:Task
    },
    children: [
      {
        path: 'parameterSet/ts21',
        name: '任务2-1',
        components: {
          appRight: Ts21
        }
      },
      {
        path: 'parameterSet/ts22',
        name: '任务2-2',
        components: {
          appRight: Ts22
        }
      },
      {
        path: 'parameterSet/ts23',
        name: '任务2-3',
        components: {
          appRight: Ts23
        }
      },
      {
        path: 'parameterSet/ts24',
        name: '任务2-4',
        components: {
          appRight: Ts24
        }
      }
    ]
  },
]
const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  let islogin = store.state.islogin
  console.log(islogin,to.meta.requireAuth)
  if (to.meta.requireAuth) {
    if (islogin) {
      next()
    } else {
        next({
          path: '/login'
          // query: {redirect: to.fullPath}
        })
    }
  }
  else {
    next()
  }
})

export default router



