<template>
  <div>
    <selectTwo :search="search"></selectTwo>
    <div class="diffWrapper" v-loading="loading">
      <div class="LRWrapper">
        <div id="left-line-chart" class="line-chart"></div>
        <div id="left-bar-chart" class="bar-chart"></div>
      </div>
      <div class="LRWrapper">
        <div id="right-line-chart" class="line-chart"></div>
        <div id="right-bar-chart" class="bar-chart"></div>
      </div>
    </div>
  </div>
</template>

<script>
import selectTwo from "@/components/basic/selectTwo";
import echarts from "echarts";
import axios from "axios";

export default {
  name: "PonFlowSpeed",
  data() {
    return {
      list: [],
      loading: false
    };
  },
  components: {
    selectTwo
  },
  methods: {
    initChart(domName) {
      let myChart = echarts.init(document.getElementById(domName));

      let categoryData = [];
      let valueData = [];
      let dataCount = 500;
      let data = generateData(dataCount);

      let option = {
        title: {
          text: echarts.format.addCommas(dataCount) + " Data",
          left: 10
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: false
            },
            saveAsImage: {
              pixelRatio: 2
            }
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          bottom: 90
        },
        dataZoom: [
          {
            type: "inside"
          },
          {
            type: "slider"
          }
        ],
        xAxis: {
          data: data.categoryData,
          silent: false,
          splitLine: {
            show: false
          },
          splitArea: {
            show: false
          }
        },
        yAxis: {
          splitArea: {
            show: false
          }
        },
        series: [
          {
            type: "bar",
            data: data.valueData,
            // Set `large` for large data amount
            large: true
          }
        ]
      };

      function generateData(count) {
        for (let i = 0; i < count; i++) {
          categoryData.push(i);
          valueData.push(Math.random() * 50);
        }

        return {
          categoryData: categoryData,
          valueData: valueData
        };
      }
      myChart.setOption(option);
    },
    search() {
      this.loading = true;

      setTimeout(() => {
        this.initLineChart("left-line-chart");
        this.initLineChart("right-line-chart");
        this.initBarChart("left-bar-chart");
        this.initBarChart("right-bar-chart");
        this.loading = false;
      }, 2000);
    },
    initLineChart(domName, data) {
      let myChart = echarts.init(document.getElementById(domName));
      let option = {
        title: {
          text: "折线图堆叠"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["邮件营销", "联盟广告", "视频广告", "直接访问", "搜索引擎"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "邮件营销",
            type: "line",
            stack: "总量",
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: "联盟广告",
            type: "line",
            stack: "总量",
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: "视频广告",
            type: "line",
            stack: "总量",
            data: [150, 232, 201, 154, 190, 330, 410]
          }
        ]
      };
      myChart.setOption(option);
    },
    initBarChart(domName, data) {
      let myChart = echarts.init(document.getElementById(domName));
      let option = {
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ["product", "2015", "2016", "2017"],
            ["Matcha Latte", 43.3, 85.8, 93.7]
          ]
        },
        xAxis: { type: "category" },
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [{ type: "bar" }, { type: "bar" }, { type: "bar" }]
      };
      myChart.setOption(option);
    }
  }
};
</script>

<style scoped lang="stylus">
.diffWrapper {
  display: flex;
  justify-content: space-around;
  height: 800px;
  width: 1200px;
  // background-color: #FF6666;
  padding: 30px;
  margin-top: 20px;

  .LRWrapper {
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
    justify-content: space-around;
    // background-color: #FFFF00;

    .line-chart {
      width: 500px;
      height: 350px;
      // background-color: #006699;
    }

    .bar-chart {
      width: 500px;
      height: 350px;
      // background-color: #006699;
    }
  }
}
</style>



