<template>
  <div class="app-container">
    <div class="app-side app-side-left">
      <sidebar :data="sidebardata" :open="defultopen"></sidebar>
    </div>
    <div class="app-right" style="overflow-y: auto; position:relative">
      <router-view name="appRight"></router-view>
    </div>
  </div>
</template>

<script>
import sidebar from '@/components/sidebar'
export default {
    data(){
        return{
        defultopen:['/task','/task/parameterSet'],
        sidebardata:{
          name:"任务",
          path:'/task',
          children:[
            {
              name: "参数设置",
              path: "/task/parameterSet",
              children:[{
                name: "业务参数表",
                path: "/task/parameterSet/ts21"
              },{
                name: "业务网络KPI参数表",
                path: "/task/parameterSet/ts22"
              },{
                name: "业务套餐设置",
                path: "/task/parameterSet/ts23"
              },{
                name: "组播用户直播频道合计带宽",
                path: "/task/parameterSet/ts24"
              }]
            }
          ]
        }
      }
    },
    components:{
        sidebar
    }
}
</script>

<style>
.app-container {
  position: relative;
  padding: 0;
  margin: 0;
  height: calc(100% - 95px);
  width: 100%;
  display: flex;
}
.app-side {
  height: 100%;
  width: 250px;
  font-weight: 700;
  color: #333;
  background-color: #fff;
  /* border-right: solid 1px grey; */
  overflow-y: scroll;
}
.app-right {
  width: calc(100% - 230px);
  height: calc(100% - 40px);
  padding: 20px;
  overflow-y: scroll;
  overflow: auto;
}
</style>



