<template>
  <div>
    <selectThree :search="search"></selectThree>
    <!-- <button @click="search"></button> -->
    <div class="TrendWrapper" v-loading="loading">
      <div class="increaseWrapper">
        <h1 v-show="titleShow">流量增长</h1>
        <div class="increaseItemWrapper">
          <div
            class="item"
            v-for="(n,index) in increaseCount"
            :key="n"
            :id="setIncreaseId(index)"
          >{{n}}</div>
        </div>
      </div>
      <div class="stableWrapper">
        <h1 v-show="titleShow">流量稳定</h1>
        <div class="stableItemWrapper">
          <div class="item" v-for="(n,index) in stableCount" :key="n" :id="setStableId(index)">{{n}}</div>
        </div>
      </div>
      <div class="decreaseWrapper">
        <h1 v-show="titleShow">流量下降</h1>
        <div class="decreaseItemWrapper">
          <div class="item" v-for="(n,index) in decreaseCount" :key="n" :id="setDecreaseId(index)">{{n}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import selectThree from "@/components/basic/selectThree";
import echarts from "echarts";
import axios from "axios";

export default {
  name: "PonFlowTrend",
  data() {
    return {
      list: [],
      viewHeight: 0,
      loading: false,
      titleShow: false,
      stableCount: 0,
      decreaseCount: 0,
      increaseCount: 0,
      stableData: [],
      decreaseData: [],
      increaseData: [],
      increaseChartData: {},
      stableChartData: {},
      decreaseChartData: {},
    };
  },
  components: {
    selectThree
  },
  methods: {
    setIncreaseId(index) {
      return "increase" + index;
    },
    setStableId(index) {
      return "stable" + index
    },
    setDecreaseId(index) {
      return "decrease" + index
    },
    initChart(domName, xData, avgData, peakData, trendLineData, title) {
      let myChart = echarts.init(document.getElementById(domName));
      let option = {
        title: {
          text: title,
          textStyle: {
            fontSize: 15
          }
        },
        tooltip: {
          show: true,
          position: 'right'
        },
        color: ["#0099FF", "#99CC00",'#F00000'],
        backgroundColor: '#fff',
        xAxis: {
          data: xData,
          axisLabel: {
            interval: 8,
            rotate: 45,
            fontSize: 8,
          },
        },
        yAxis: {},
        grid: {
          // top: 10,
          bottom: 45,
          left: 30,
        },
        series: [
          {
            symbolSize: 5,
            type: "scatter",
            data: avgData,
            label: {
              // show: true
            }
          },
          {
            symbolSize: 5,
            data: peakData,
            type: "scatter",
          },
          {
            symbolSize: 10,
            data: trendLineData,
            type: "line",
          }
        ]
      };

      myChart.setOption(option);
      // window.addEventListener("resize", function() {
      //   myChart.resize();
      // });
    },
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      let station = this.$store.getters.getStation;
      let olt = this.$store.getters.getOlt
      let traffic_tag = this.$store.getters.getTrafficTag
      axios({
        method: "get",
        url: "/api/aaa/pon-port/trend/",
        params: {
          department_name: dep,
          city_name: '天津',
          station: station,
          olt_name: olt,
          tag: traffic_tag
        }
      })
      // axios({
      //   method: "get",
      //   url: "/"
      // })
        .then(res => {
          let data = res.data.data;
          this.stableCount = 0;
          this.decreaseCount = 0;
          this.increaseCount = 0;
          this.stableData = [];
          this.decreaseData = [];
          this.increaseData = [];
          data.map((item, index) => {
            console.log(item.type);
            switch (item.type) {
              case "STABLE":
                this.stableCount++;
                item.id = index;
                this.stableData.push(item);
                break;
              case "DECREASE":
                this.decreaseCount++;
                item.id = index;
                this.decreaseData.push(item);
                break;
              case "INCREASE":
                this.increaseCount++;
                item.id = index;
                this.increaseData.push(item);
                break;
              default:
                break;
            }
          });
        })
        .then(() => {
          this.titleShow = true
          console.log(this.stableData, this.decreaseCount, this.increaseCount);
          this.dataPreProcess(this.increaseData,'increase',this.increaseChartData)
          this.dataPreProcess(this.stableData,'stable',this.stableChartData)
          this.dataPreProcess(this.decreaseData,'decrease',this.decreaseChartData)
            this.loading = false
            this.lazyload()
            this.addScroll()
          });
    },
    addScroll() {
      let dom = document.querySelector('.TrendWrapper')
      dom.addEventListener('scroll', this.throttling(this.lazyload,100, 1000))
    },
    throttling(fn, wait, maxTimelong) {
      // 节流函数
      let timeout = null
      let startTime = +new Date()
      return function () {
        if (timeout !== null) clearTimeout(timeout);
        let curTime = +new Date()
        if (curTime - startTime >= maxTimelong) {
          fn()
          startTime = curTime
        } else {
          timeout = setTimeout(fn, wait)
        }
      }
    },
    lazyload() {
      let ele = document.querySelectorAll('.item')
      console.log(this.viewHeight)
      Array.prototype.forEach.call(ele, (item, index) => {
        let rect
        if (item.dataset.chartLazy === '') {
          return
        }
        rect = item.getBoundingClientRect()

        if (rect.bottom > 0 && rect.top < this.viewHeight) {
          // console.log(item.id[0])
          let data
          switch (item.id[0]) {
            case 'i':
              data = this.increaseChartData[item.id]
              break;
            case 's':
              data = this.stableChartData[item.id]
              break;
            case 'd':
              data = this.decreaseChartData[item.id]
              break;
            default:
              break;
          }
          this.initChart(data.name, data.xData, data.avgData, data.peakData, data.trendLineData,data.title)
        }
      })
    },
    dataPreProcess(data,datatype,dataDes) {
      data.map((item, index) => {
      let name = `${datatype}${index}`
      dataDes[name] = {}
      let that = dataDes[name]
      that.name = name
      that.xData = item.samples.x
      that.avgData = item.samples.avg
      that.peakData = item.samples.peak
      let tl = item.trend_line
      that.tl = item.trend_line
      that.trendLineData = [[tl.x1,tl.y1],[tl.x2,tl.y2]]
      that.title = `${item.olt}--${item.pon_board}--${item.pon_port}`
      })
    }
  },
    mounted() {
      this.viewHeight = document.documentElement.clientHeight
    },
};
</script>

<style scoped lang="stylus">
.TrendWrapper {
  display: flex;
  justify-content: space-between;
  height: 550px;
  overflow: auto;
  .increaseWrapper {
    width: 410px;
    // background: #CCCC33;

    .increaseItemWrapper {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .item {
        width: 200px;
        height: 250px;
        background: #D6DBDF;
        margin: 5px 0;
      }
    }
  }

  .stableWrapper {
    width: 410px;
    // background: #FFFF99;

    .stableItemWrapper {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    .item {
      width: 200px;
      height: 250px;
      background: #D6DBDF;
      margin: 5px 0;
    }
  }

  .decreaseWrapper {
    width: 410px;
    // background: #CC9933;

    .decreaseItemWrapper {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    .item {
      width: 200px;
      height: 250px;
      background: #D6DBDF;
      margin: 5px 0;
    }
  }
}
</style>



